	"markerCategoryId": "1946",
	"markerCategoryTypeId": "1",
	"userId": "1",
	"userName": "|N|NjA|",
	"name": "Journal of Various Worries",
	"description": "GameID: -1105632466",
	"x": "155.8359375",
	"y": "-143.5234375",
	"jumpMakerId": "0",
	"tabId": "3300",
	"tabTitle": "",
	"tabText": "<p>Page 1</p>\r\n<p style=\"padding-left: 30px;\">Well, my wife left me today. The last thing she said to me was: \"What's more important, the Cuccos or me?!\" I chose the Cuccos. - The Bow Master</p>\r\n<p style=\"padding-left: 30px;\">&nbsp;</p>\r\n<p>Page 2</p>\r\n<p style=\"padding-left: 30px;\">A painter unfamiliar to any of us has arrived in the village. He claims he is searching for the Great Fairy's Fountain, but that's near where the village's spirit protector resides. We must make sure the outsider doesn't learn of the location of the fountain...or the two shrines near it. -Anonymous Father of Two</p>\r\n<p style=\"padding-left: 30px;\">&nbsp;</p>\r\n<p>Page 3</p>\r\n<p style=\"padding-left: 30px;\">That new guy in town looks like he really knows how to tune a bow. I hope he comes by and nocks an arrow for me. It's been WAAAY too long. -Lonely Arrow Girl</p>\r\n<p style=\"padding-left: 30px;\">&nbsp;</p>\r\n<p>Page 4</p>\r\n<p style=\"padding-left: 30px;\">Day in, day out, I'm outside barking people into the shop. I don't mind, really. It's good money, and busying myself helps to numb the pain. But the only thing that really helps me feel better is walking among the fireflies. Unfortunately, we've now been asked to not walk around at night because it's dangerous. I don't know how I can cope without the fireflies... -Struggling Shop Girl</p>\r\n<p style=\"padding-left: 30px;\">&nbsp;</p>\r\n<p>Page 5</p>\r\n<p style=\"padding-left: 30px;\">There is a precious heirloom that has been passed down through my family for generations. Recently, I've begun to hear voices telling me that the heirloom has an important destiny. It's going to happen soon. It makes me nervous, but I can feel it in my bones. -Papaya</p>\r\n<p style=\"padding-left: 30px;\">&nbsp;</p>",
	"tabUserId": "1",
	"tabUserName": "|N|NjA|",
	"globalMarker": "0",
	"visible": "1"
