	"userId": "96",
	"userName": "QweenB",
	"name": "Link's House",
	"description": "This is not the actual side quest name. We need to confirm in-game. -Josh",
	"x": "179.703125",
	"y": "-163.765625",
	"jumpMakerId": "0",
	"tabId": "779",
	"tabTitle": "",
	"tabText": "<p>So far, the only known house for sale is in Hateno Village, near the back end of the village. If players go to the area, they will see some NPCs working on a broken down house. Players can talk to them and purchase the house if they have right amount of money.<br /><br />The house price can be further dropped if players are willing to contribute 30 bundles of wood. Chopping down wood isn&rsquo;t very difficult so players should go for this option instead of spending a huge amount on buying the house. All they need to do is grab an axe and start cutting down any trees they come across.</p>\r\n<div id=\"&ldquo;nativo_middle&rdquo;\">&nbsp;To make up the rest of the money, stones and gems acquired while adventuring can be sold to various vendors. Once players have the funds and 30 bundles of wood, they can go back to the house and purchase it, giving them a place to rest at.</div>",
	"tabUserId": "2",
	"tabUserName": "Joshua",
	"globalMarker": "0",
	"visible": "1"
