	"jumpMakerId": "0",
	"tabId": "346<|>347<|>348",
	"tabTitle": "<|><|>",
	"tabText": "<p><img src=\"http://zeldauniverse.net/wp-content/uploads/2017/03/map-teebo.jpg\" alt=\"\" width=\"1280\" height=\"720\" />Near the entrance to Hateno Village, speak to Teebo, a young boy, and agree to let him show you what he found. He will lead you around a nearby cliff to the <a title=\"Horned Statue\" href=\"?game=BotW&amp;marker=450&amp;zoom=8\">Horned Statue</a>.&nbsp;</p>\r\n<p>&nbsp;</p><|><p><img src=\"http://zeldauniverse.net/wp-content/uploads/2017/03/map-horned-statue-heart-removal.jpg\" alt=\"\" width=\"1280\" height=\"720\" />Speak to the Horned Statue, and it will take a Heart Container from you and then tell you the story of how it came to be forgotten.&nbsp;</p>\r\n<p>Speak to the Horned Statue again and demand that it return your Heart Container. The statue will return the Heart Container and then explain how it will allow you to trade Heart Containers for Stamina Vessels. &nbsp;The side quest will then be marked as completed.</p>\r\n<p>&nbsp;</p><|><p><img src=\"http://zeldauniverse.net/wp-content/uploads/2017/03/map-horned-statue.jpg\" alt=\"\" width=\"1280\" height=\"720\" />You can return to the Horned Statue at any time to exchange Heart Containers and Stamina Vessels.&nbsp;</p>\r\n<p>&nbsp;</p>",
