	"x": "159.875",
	"y": "-150.4296875",
	"jumpMakerId": "0",
	"tabId": "229",
	"tabTitle": "",
	"tabText": "<p>When Link encounters a Korok, it believes it has been found by Hestu before realizing that Link is not Hestu. The Korok will then give Link a 克洛洛种子, asking him to return it to Hestu.</p>\r\n<p>Upon finding Hestu, Link will discover that his Maracas were stolen from him by his fellow Koroks as part of a prank. Talking to him will trigger The Priceless Maracas Side Quest, in which Link will have to retrieve his Maracas from a chest in a nearby enemy camp. Once he returns his Maracas to him, Hestu will find out that the 克洛洛种子s inside the Maracas are gone. Hestu will then tell Link that if Hestu had a Koroko Seed, he would be able to sing, dance and use his powers to expand Link's Inventory. Hestu can upgrade Link's Weapon Stash, Bow Stash, or Shield Stash.</p>\r\n<p><a title=\"Learn more about Hestu&nbsp;at Zelda Wiki\" href=\"https://zeldawiki.org/Hestu\">Learn more about Hestu&nbsp;at Zelda Wiki</a><br /><a title=\"克洛洛种子 Inventory Upgrades Guide\" href=\"http://zeldauniverse.net/guides/breath-of-the-wild/sidequests/korok-seed-inventory-upgrades/\">克洛洛种子 Inventory Upgrades Guide</a></p>",
