<template>
<el-container>
    <el-header>编辑攻略</el-header>
    <el-main>
        
        <el-form ref="form" :model="form">
            <el-form-item label="标题" class="item">
                <el-input v-model="form.title"></el-input>
            </el-form-item>
            <el-form-item label="内容" class="item">
                
            </el-form-item> 
            <el-form-item label="" class="item">
                
                <div ref="editor" style="text-align:left"></div>
                
                
            </el-form-item> 
            <el-form-item >
                
            </el-form-item>
            <el-form-item label="">
                <el-button type="primary" @click="onSubmit">提交</el-button>
                <el-button @click="goBack">返回</el-button>
            </el-form-item>
        </el-form>
    </el-main>
</el-container>
  

</template>

<script>
import E from "wangeditor";
import AV from "leancloud-storage";
export default {
  data() {
    return {
      form: {
        title: ""
      },
      editorContent: "",
      markInfoObj: AV.Object.extend("markInfo") //声明mark对象
    };
  },
  methods: {
    getContent() {
      console.log(this.editorContent);
    },
    onSubmit() {
      if (this.editorContent == "" || this.editorContent == null) {
        this.$layer.alert("请输入内容!");
        return;
      }
      let that = this;
      if (AV.User.current() != null) {
        //登录状态
        let user = AV.User.current().attributes.username; //不存在就创建一个信息
        let markInfo = new this.markInfoObj(); //创建一个点位评论
        markInfo.set("userName", user);
        markInfo.set("message", this.form.title);
        markInfo.set("markId", this.$route.params.id);
        markInfo.set("content", this.editorContent);
        markInfo.set("count", 0); //赞
        markInfo.save().then(
          function(todo) {
            //console.log('objectId is ' + todo.id);
            //that.$store.dispatch('updateLikesCount',that.$store.state.markId)
            that.$layer.alert("评论成功!");
            //that.markInfo.message = ''
          },
          function(error) {
            that.$layer.alert("出了点问题，请稍后再试!"); //保存失败提示
            console.error(error);
          }
        );
      } else {
        this.$layer.alert("请登录后评论!");
      }
    },
    goBack() {
      this.$router.back();
    }
  },
  mounted() {
    var editor = new E(this.$refs.editor);
    editor.customConfig.onchange = html => {
      this.editorContent = html;
    };
    editor.customConfig.zIndex = 100;
    editor.customConfig.menus = [
      "head", // 标题
      "bold", // 粗体
      "fontSize", // 字号
      "fontName", // 字体
      "foreColor", // 文字颜色
      "link", // 插入链接
      "justify", // 对齐方式
      "quote", // 引用
      "image", // 插入图片
      "video", // 插入视频
    ];
    editor.create();

    //console.log(this.$route.params);
  }
};
</script>


<style>
.el-header,
.el-footer {
  /* background-color: #b3c0d1; */
  /* color: #333; */
  text-align: center;
  line-height: 6rem;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 20rem;
}

.el-main {
  /* background-color: #e9eef3; */
  /* color: #333; */
  text-align: center;
  line-height: 2rem;
}

body > .el-container {
  margin-bottom: 4rem;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 26rem;
}

.el-container:nth-child(7) .el-aside {
  line-height: 32rem;
}

.item {
  margin-bottom: 0rem !important;
}
</style>




