import Vue from 'vue'
import Vuex from 'vuex'
import AV from "leancloud-storage";

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        markId:'',
        infoData:[{
            name:'',
            info:'',
            count:0
        }],
        routerObj:{}
    },
    actions:{
        getMarkInfo(context,markId){
            
            context.commit('setMarkId',markId)
            let query = new AV.Query('markInfo')
            query.equalTo('markId',markId)
            query.addDescending('count')
            query.addDescending('createdAt')
            query.limit(20);// 最多返回 20 条结果
            let markInfoData = []
            query.find().then((todo)=>{
                //console.log(todo)
                for(let i=0;i<todo.length;i++) {
                    let markInfoTmp = {}
                    markInfoTmp.name = todo[i].attributes.userName
                    markInfoTmp.uid = todo[i].id
                    markInfoTmp.info = todo[i].attributes.message
                    markInfoTmp.count = todo[i].attributes.count
                    markInfoTmp.markId = todo[i].attributes.markId
                    markInfoData.push(markInfoTmp)
                }
                context.commit('getMarkInfo',markInfoData)
            })
        },
        updateLikesCount(context,markId){
            let query = new AV.Query('markInfo')
            query.equalTo('markId',markId)
            query.addDescending('count')
            query.addDescending('createdAt')
            query.limit(20);// 最多返回 20 条结果
            let markInfoData = []
            query.find().then((todo)=>{
                //console.log(todo)
                for(let i=0;i<todo.length;i++) {
                    let markInfoTmp = {}
                    markInfoTmp.name = todo[i].attributes.userName
                    markInfoTmp.uid = todo[i].id
                    markInfoTmp.info = todo[i].attributes.message
                    markInfoTmp.count = todo[i].attributes.count
                    markInfoTmp.markId = todo[i].attributes.markId
                    markInfoData.push(markInfoTmp)
                }
                context.commit('getMarkInfo',markInfoData)
            })
        }
    },
    mutations:{
        setMarkId(state,markId) {
            //console.log('zzz',markId)
            state.markId = markId
        },
        getMarkInfo(state,markInfoData){
            state.infoData = markInfoData
        },
        setRouterObj(state,routerObj){
            state.routerObj = routerObj
        }

    }

})


