const index = r => require.ensure([], () => r(require('./page/index')), 'index');
const login = r => require.ensure([], () => r(require('./page/login')), 'login');
const sign = r => require.ensure([], () => r(require('./page/sign')), 'sign');
const info = r => require.ensure([], () => r(require('./page/info')), 'info');
import test from './page/test'
import editRaider from './page/editRaider'
import contentPage from './page/contentPage'
import sponsor from './page/sponsor'
import appdownload from './page/appdownload'
const routes = [{
    path: '/',
    meta: {
      requireAuth: false, //
      txt: '首页',
      flag: 'index'
    },
    component: index
  },
  {
    path: '/index',
    meta: {
      requireAuth: false,
      txt: 'index',
      flag: 'index'
    },
    component: index
  },
  {
    path: '/login',
    meta: {
      requireAuth: false,
      txt: 'login',
      flag: 'login'
    },
    component: login
  },
  {
    path: '/sign',
    meta: {
      requireAuth: false,
      txt: 'sign',
      flag: 'sign'
    },
    component: sign
  },
  {
    path: '/info',
    meta: {
      requireAuth: false,
      txt: 'info',
      flag: 'info'
    },
    component: info
  },
  {
    path: '/test',
    meta: {
      requireAuth: false,
      txt: 'test',
      flag: 'test'
    },
    component: test
  },
  {
    path:'/editRaider',
    name:'editRaider',
    component :editRaider
  },
  {
    path:'/contentPage',
    name:'contentPage',
    component :contentPage
  },
  {
    path:'/sponsor',
    name:'sponsor',
    component :sponsor
  },
  {
    path:'/appdownload',
    name:'appdownload',
    component :appdownload
  },
];
  
  
  
  export default routes
  


