<template>
<el-container>
    <el-header>
        攻略详情
        
    </el-header>
    <el-button @click="$router.back()">返回</el-button>
    <el-main>
        <h1 style=" text-align: center">{{title}}</h1>
        <div ref="test" v-html="content"></div>        
    </el-main>
</el-container>
  

</template>

<script>
import AV from "leancloud-storage";
export default {
  data() {
    return {
      title: "",
      content: ""
    };
  },

  methods: {
    getContent() {
      let that = this;
      let query = new AV.Query("markInfo");
      query.get(this.$route.params.id).then(todo => {
        //console.log(todo);
        that.title = todo.attributes.message
        that.content = todo.attributes.content
        
      })
    },
    setTagAttribute(){
      var qq = document.getElementsByTagName('a')
    }
  },
  watch:{
    // content:function(newdate){
    //   //console.log(newdate)
    //   this.content = newdate.replace('target="_blank"','target="_self"')  //直接操作DOM无效 只能这样了
    // }
  },
  mounted() {
    this.getContent()
    
  }
};
</script>


<style>
.el-header,
.el-footer {
  /* background-color: #b3c0d1; */
  /* color: #333; */
  text-align: center;
  line-height: 6rem;
}
</style>




